import server from '../../server.js';
import md5 from '../../static/md5.js';
export default {
	data() {
		return {
			xzId:1,
			showVip:false,
			gaodu:'-710px',
			yanse:'rgba(0,0,0,0)',
			userInfo:[],
			monnumber:0,
			money:0
		}
	},
	onLoad:function(){
		this.userInfo=server.userinfo
	},
	methods: {
		getVip:function(){
			if(this.monnumber>=1){
				if(this.monnumber==1){
					this.money=1
				}else{
					this.money=this.monnumber*20-this.monnumber*4
				}
				console.log('月份：'+this.monnumber,'金额：'+this.money)
				let sjstr=md5(String(Date.now()));
				let time=String(Date.now())
				let signTemp="appId=wxb2418420ae2cf37c&nonceStr="+sjstr+"&package=prepay_id=wxpay&signType=MD5&timeStamp="+time+"&key=xinfenghuliankejiyouxiangongsi12"
				let sign=md5(signTemp)
				uni.requestPayment({
					provider: 'wxpay',
					timeStamp: time,
					nonceStr: sjstr,
					package: 'prepay_id=wxpay',
					signType: 'MD5',
					paySign: sign,
					success: function (res) {
						console.log('success:' + JSON.stringify(res));
					},
					fail: function (err) {
						console.log('fail:' + JSON.stringify(err));
					}
				});
			}else{
				uni.showToast({
					title: "必须选择月份",
					position:'bottom',
					icon:'none',
					position:'center'
				})
			}
		},
		showKaitong:function(){
			this.showVip=true
			this.gaodu='0px'
			this.yanse='rgba(0,0,0,0.4)'
		},
		inputVip:function(e){
			this.monnumber=e.target.value
		},
		hideVip:function(){
			this.showVip=false
			this.gaodu='-710px';
			this.yanse='rgba(0,0,0,0)'
			
		},
		xuanze:function(e){
			switch (e){
				case 1:
					this.monnumber=1
					break;
				case 2:
					this.monnumber=3
					break;
				case 3:
					this.monnumber=12
					break;
				case 4:
					break;
				default:
					this.monnumber=0
					break;
			}
			this.xzId=e
			/* 新思路,点赞改变列表状态可以使用该方法,:class="当前ID是否等于列子ID，如果是那么就点赞，如果不是就没有点赞，"
			但是,这样只能存在一个点赞状态,其他的状态就会消失,带思考 */
		}
	}
}
